import React from 'react'
import styles from "../../style.module.css"
import { get2DigitNo } from '../../NepaliDateConverter'

const Day = ({ onSelect, className, day, engday, setIsOpen, month, year, setInputdate, format }) => {
    const handlesubmit = () => {
        setInputdate(`${year}-${get2DigitNo(month + 1)}-${get2DigitNo(engday)}`);
        onSelect(`${year}-${get2DigitNo(month + 1)}-${get2DigitNo(engday)}`);
        setIsOpen(false);
    }
    return (
        <button className={`${styles.daybutton} ${className}`} onClick={handlesubmit}>{day}</button>
    )
}

export default Day
